* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.register-tagline {
  text-align: center;
  color: #000; /* solid black */
  font-size: 22px;
  font-weight: 600;
  margin-bottom: 25px;
  font-family: 'Poppins', sans-serif;
  letter-spacing: 0.5px;
  text-shadow: 1px 1px rgba(255, 255, 255, 0.2); /* subtle glow for red bg */
}

/*transection page*/
.filters{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0 3px grey;
}

/*transection page*/

/*antd icons */
.switch-icons{
  border: 1px solid rgba(0, 0, 0, 0.652);
  border-radius: 5px ;
  padding: 10px 15px;
}

.anticon svg{
  font-size: 20px;
  cursor: pointer;
}

.active-icon{
  color: black;
}

.inactive-icon{
  color: rgb(147, 139, 139);
}