.register-page {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;

  /* Animated gradient background â€“ bold peach tones */
  background: linear-gradient(-45deg, #ffd1a1, #ffb08c, #f9a5c1, #ffd1a1);
  background-size: 400% 400%;
  animation: boldPeachFlow 12s ease infinite;
  font-family: "Poppins", sans-serif;
}

@keyframes boldPeachFlow {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.auth-container {
  background: rgba(255, 255, 255, 0.9);
  padding: 40px 50px;
  border-radius: 16px;
  box-shadow: 6px 8px 25px rgba(0, 0, 0, 0.15);
  width: 420px;
  text-align: center;
  transition: all 0.3s ease;
}

.auth-container:hover {
  transform: translateY(-3px);
  box-shadow: 6px 8px 30px rgba(0, 0, 0, 0.25);
}

.auth-container h2 {
  color: #333;
  font-size: 26px;
  font-weight: 600;
  margin-bottom: 10px;
}

.auth-container p {
  color: #666;
  font-size: 14px;
  margin-bottom: 30px;
}

/* AntD button style */
button {
  background-color: #fa8c16;
  color: white;
  width: 100%;
  border: none;
  border-radius: 8px;
  padding: 10px 0;
  font-size: 16px;
  cursor: pointer;
  transition: 0.3s ease;
}

button:hover {
  background-color: #d46b08;
}

a {
  color: #1890ff;
  font-weight: 500;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}