/* Home Page Styling */
.home-page {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  background: linear-gradient(135deg, #ffb6b9, #fca3d9, #fcd5ce, #fcb9b2);
  background-size: 300% 300%;
  animation: gradientmove 12s ease infinite;
  padding-top: 35px;   /* keep the same top space */
  padding-bottom: 0;   /* no extra bottom padding */
}

/* Smooth moving gradient effect */
@keyframes gradientmove {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

/* Main content container */
.content-container {
  background-color: white;
  min-height: 80vh;
  width: calc(100% - 1cm); /* 0.5 cm margin on each side */
  max-width: none;
  margin: 0 auto 3px auto; /* about 0.5 cm above the footer */
  border-radius: 20px;
  box-shadow: 0px 2px 25px rgba(0, 0, 0, 0.1);
  padding: 30px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  box-sizing: border-box;
}